body {
  margin: 0;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
}

.img__logo {
  align-items: center;
  display: flex;
}

.header {
  flex-direction: row;
  justify-content: space-between;
  padding: 0 50px;
  display: flex;
}

ul {
  margin: 0;
  padding: 0;
}

.nav__list {
  text-transform: uppercase;
  display: flex;
}

.nav__item {
  display: flex;
}

.nav__item:nth-child(n+2) {
  margin-left: 20px;
}

.is-active {
  position: relative;
}

.is-active:after {
  content: "";
  background: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.nav__link {
  color: #000;
  padding: 23px 0;
  text-decoration: none;
}

.nav__link:hover {
  color: #00acdc;
}

.nav__link.is-active {
  color: #00acdc;
  text-decoration: none;
}
